
export const classes = () => {
//–†–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –∫–ª–∞—Å—Å, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –æ–∫—Ä—É–∂–Ω–æ—Å—Ç—å. –í –∫–ª–∞—Å—Å–µ –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å —Å–ª–µ–¥—É—é—â–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã:
//–ø–æ–ª–µ, —Ö—Ä–∞–Ω—è—â–µ–µ —Ä–∞–¥–∏—É—Å –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏;
//get-—Å–≤–æ–π—Å—Ç–≤–æ, –≤–æ–∑–≤—Ä–∞—â–∞—é—â–µ–µ —Ä–∞–¥–∏—É—Å –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏;
//set-—Å–≤–æ–π—Å—Ç–≤–æ, —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞—é—â–µ–µ —Ä–∞–¥–∏—É—Å –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏;
//get-—Å–≤–æ–π—Å—Ç–≤–æ, –≤–æ–∑–≤—Ä–∞—â–∞—é—â–µ–µ –¥–∏–∞–º–µ—Ç—Ä –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏;
//–º–µ—Ç–æ–¥, –≤—ã—á–∏—Å–ª—è—é—â–∏–π –ø–ª–æ—â–∞–¥—å –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏;
//–º–µ—Ç–æ–¥, –≤—ã—á–∏—Å–ª—è—é—â–∏–π –¥–ª–∏–Ω—É –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏.
//–ü—Ä–æ–¥–µ–º–æ–Ω—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å —Ä–∞–±–æ—Ç—É —Å–≤–æ–π—Å—Ç–≤ –∏ –º–µ—Ç–æ–¥–æ–≤. 
    document.getElementById("circleClass").onclick = function circleClass() {        
        class Circle {
            constructor(radius) {
                this.radius = radius;
            }

            get circleRadius() {
                return this.radius;
            }
            set circleRadius(newRadius) {
                this.radius = newRadius;
            }
            get circleDiameter() {
                return this.radius * 2;
            }
            circleArea() {
                return 3.14159265359 * (this.radius**2);
            }
            circleLenght() {
                return 3.14159265359 * this.circleDiameter;
            }
        }
        const circle = new Circle(4);
        const changeRadius = +prompt(`–í–≤–µ–¥–∏—Ç–µ —Ä–∞–¥–∏—É—Å –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏, —Å–º.`, "4");
        circle.circleRadius = changeRadius;
        alert(` –î–ª–∏–Ω–∞ –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏ ${changeRadius} —Å–º.\n –î–∏–∞–º–µ—Ç—Ä –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏ = ${circle.circleDiameter.toFixed(2)} —Å–º.\n –ü–ª–æ—â–∞–¥—å –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏ = ${circle.circleArea().toFixed(2)} —Å–º.\n –î–ª–∏–Ω–∞ –æ–∫—Ä—É–∂–Ω–æ—Å—Ç–∏ = ${circle.circleLenght().toFixed(2)} —Å–º.`);
    };


//–†–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –∫–ª–∞—Å—Å, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –ø—Ä–æ—Å—Ç–æ–π –º–∞—Ä–∫–µ—Ä. –í –∫–ª–∞—Å—Å–µ –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å —Å–ª–µ–¥—É—é—â–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã:
//–ø–æ–ª–µ, –∫–æ—Ç–æ—Ä–æ–µ —Ö—Ä–∞–Ω–∏—Ç —Ü–≤–µ—Ç –º–∞—Ä–∫–µ—Ä–∞;
//–ø–æ–ª–µ, –∫–æ—Ç–æ—Ä–æ–µ —Ö—Ä–∞–Ω–∏—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —á–µ—Ä–Ω–∏–ª –≤ –º–∞—Ä–∫–µ—Ä–µ (–≤ –ø—Ä–æ—Ü–µ–Ω—Ç–∞—Ö);
//–º–µ—Ç–æ–¥ –¥–ª—è –ø–µ—á–∞—Ç–∏ (–º–µ—Ç–æ–¥ –ø—Ä–∏–Ω–∏–º–∞–µ—Ç —Å—Ç—Ä–æ–∫—É –∏ –≤—ã–≤–æ–¥–∏—Ç —Ç–µ–∫—Å—Ç —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–º —Ü–≤–µ—Ç–æ–º; —Ç–µ–∫—Å—Ç –≤—ã–≤–æ–¥–∏—Ç—Å—è –¥–æ —Ç–µ—Ö –ø–æ—Ä, –ø–æ–∫–∞ –≤ –º–∞—Ä–∫–µ—Ä–µ –µ—Å—Ç—å —á–µ—Ä–Ω–∏–ª–∞; –æ–¥–∏–Ω –Ω–µ –ø—Ä–æ–±–µ–ª—å–Ω—ã–π —Å–∏–º–≤–æ–ª ‚Äì —ç—Ç–æ 0,5% —á–µ—Ä–Ω–∏–ª –≤ –º–∞—Ä–∫–µ—Ä–µ).
//–†–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –∫–ª–∞—Å—Å, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π –∑–∞–ø—Ä–∞–≤–ª—è—é—â–∏–π—Å—è –º–∞—Ä–∫–µ—Ä, —É–Ω–∞—Å–ª–µ–¥–æ–≤–∞–≤ –µ–≥–æ –æ—Ç –ø—Ä–æ—Å—Ç–æ–≥–æ –º–∞—Ä–∫–µ—Ä–∞ –∏ –¥–æ–±–∞–≤–∏–≤ –º–µ—Ç–æ–¥ –¥–ª—è –∑–∞–ø—Ä–∞–≤–∫–∏ –º–∞—Ä–∫–µ—Ä–∞.
//–ü—Ä–æ–¥–µ–º–æ–Ω—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å —Ä–∞–±–æ—Ç—É –Ω–∞–ø–∏—Å–∞–Ω–Ω—ã—Ö –º–µ—Ç–æ–¥–æ–≤. 
document.getElementById("markerClass").onclick = function markerClass() { 
    class Marker {
        constructor(_color, _ink) {//–≥–¥–µ –ø—Ä–∞–≤–∏–ª—å–Ω–æ –Ω–∞—á–∏–Ω–∞—Ç—å –Ω–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ —Å _? this._color - –∑–∞—â–∏—â–µ–Ω–Ω–æ–µ?
          this.color = _color;
          this.ink = _ink;
        }
        print(text) {
          console.log(text.length);
          let styles = `color: ${this.color}`;
          let textLength = Math.floor((100 / 0.5) * this.ink);
          let spaces = 0;
          for (let i = 0; i < textLength; i++) {
            if (text[i] == " ") {
              spaces++;
            }
          }
          text = text.substr(0, textLength + spaces);
          console.log(textLength);
          document.write(`<p style= "${styles}">${text}</p>`);
        }
      }
      let  redMarker = new Marker("red", 0.9);
      let greenMarker = new Marker("green", 0.5);
     let yellowMarker = new Marker("yellow", 0.7);
      greenMarker.print(
        "–ê –≤—ã –∑–Ω–∞–µ—Ç–µ, —á—Ç–æ –ø–µ—Ä–≤—ã–º IT-—Å–ø–µ—Ü–∏–∞–ª–∏—Å—Ç–æ–º –±—ã–ª–∞ –∂–µ–Ω—â–∏–Ω–∞ - –±—Ä–∏—Ç–∞–Ω–∫–∞ –ê–¥–∞ –õ–∞–≤–ª–µ–π—Å? –ò–º–µ–Ω–Ω–æ –æ–Ω–∞ –≤ 19 –≤–µ–∫–µ –Ω–∞–ø–∏—Å–∞–ª–∞ –∞–ª–≥–æ—Ä–∏—Ç–º –¥–ª—è –∞–Ω–∞–ª–∏—Ç–∏—á–µ—Å–∫–æ–≥–æ –∞–≥—Ä–µ–≥–∞—Ç–æ—Ä–∞, - —Ç–∞–∫ —Å–∫–∞–∑–∞—Ç—å, –ø—Ä–µ–¥–∫–∞ —Å–æ–≤—Ä–µ–º–µ–Ω–Ω–æ–≥–æ –∫–æ–º–ø—å—é—Ç–µ—Ä–∞."
      );
      redMarker.print(
        " –≠—Ç–∞ –º–∞—à–∏–Ω–∞ –±—ã–ª–∞ –∏–Ω–Ω–æ–≤–∞—Ü–∏–µ–π –≤ —Å–≤–æ–µ –≤—Ä–µ–º—è –∏ –º–æ–≥–ª–∞ —Ä–µ—à–∏—Ç—å —É—Ä–∞–≤–Ω–µ–Ω–∏–µ –ë–µ—Ä–Ω—É–ª–ª–∏, —Ç–æ –µ—Å—Ç—å –≤—ã—Å—á–∏—Ç–∞—Ç—å –∑–∞–∫–æ–Ω —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —ç–Ω–µ—Ä–≥–∏–∏, –Ω–∞—Ö–æ–¥—è—â–µ–π—Å—è –≤ –¥–≤–∏–∂–µ–Ω–∏–∏ –∂–∏–¥–∫–æ—Å—Ç–∏."
      );
      yellowMarker.print("–ê–¥–∞, –Ω–µ –∑–Ω–∞—è –≤ —Ç–æ –≤—Ä–µ–º—è, —á—Ç–æ –Ω–∞—á–∏–Ω–∞–µ—Ç –æ–≥—Ä–æ–º–Ω–æ–µ it-–¥–≤–∏–∂–µ–Ω–∏–µ, —Ä–∞–∑—Ä–∞–±–æ—Ç–∞–ª–∞ –ø–æ—Å–ª–µ–¥–æ–≤–∞—Ç–µ–ª—å–Ω–æ—Å—Ç—å –æ–ø–µ—Ä–∞—Ü–∏–π –¥–ª—è —Ä–µ—à–µ–Ω–∏—è –∑–∞–¥–∞—á–∏, —á—Ç–æ, –ø–æ —Å—É—Ç–∏, –∏ —è–≤–ª—è–µ—Ç—Å—è –Ω–∞–ø–∏—Å–∞–Ω–∏–µ–º –ø—Ä–æ–≥—Ä–∞–º–º—ã.");
      class FillMarker extends Marker {
        constructor(_color, _ink, _newInk) {
          super(_color, _ink);
          this.newInk = _newInk;
        }
        print(text) {
          console.log(text.length);
          let styles = `color: ${this.color}`;
          let textLength = Math.floor((100 / 0.5) * (this.ink + this.newInk));
          let spaces = 0;
          for (let i = 0; i < textLength; i++) {
            if (text[i] == " ") {
              spaces++;
            }
          }
          text = text.substr(0, textLength + spaces);
          console.log(textLength);
          document.write(`<p style= "${styles}">${text}</p>`);
        }
      }
      let blueMarker = new FillMarker("blue", 0.4, 0.1);
      blueMarker.print("–≠—Ç–æ –≥–æ–≤–æ—Ä–∏—Ç –æ —Ç–æ–º, —á—Ç–æ –ø—Ä–æ–≥—Ä–∞–º–º–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ–¥–≤–ª–∞—Å—Ç–Ω–æ –ª—é–±–æ–º—É —á–µ–ª–æ–≤–µ–∫—É –≤–Ω–µ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –ø–æ–ª–∞, –±—ã–ª–æ –±—ã –∂–µ–ª–∞–Ω–∏–µ.");
     
};


//–†–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –∫–ª–∞—Å—Å Employee, –æ–ø–∏—Å—ã–≤–∞—é—â–∏–π —Ä–∞–±–æ—Ç–Ω–∏–∫–∞, –∏ —Å–æ–∑–¥–∞—Ç—å –º–∞—Å—Å–∏–≤ —Ä–∞–±–æ—Ç–Ω–∏–∫–æ–≤ –±–∞–Ω–∫–∞.
//–†–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –∫–ª–∞—Å—Å EmpTable –¥–ª—è –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ HTML-–∫–æ–¥–∞ —Ç–∞–±–ª–∏—Ü—ã —Å–æ —Å–ø–∏—Å–∫–æ–º —Ä–∞–±–æ—Ç–Ω–∏–∫–æ–≤ –±–∞–Ω–∫–∞. 
//–ú–∞—Å—Å–∏–≤ —Ä–∞–±–æ—Ç–Ω–∏–∫–æ–≤ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å —á–µ—Ä–µ–∑ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä, –∞ –ø–æ–ª—É—á–∞—Ç—å HTML-–∫–æ–¥ —Å –ø–æ–º–æ—â—å—é –º–µ—Ç–æ–¥–∞ getHtml().
//–°–æ–∑–¥–∞—Ç—å –æ–±—ä–µ–∫—Ç –∫–ª–∞—Å—Å–∞ EmpTable –∏ –≤—ã–≤–µ—Å—Ç–∏ –Ω–∞ —ç–∫—Ä–∞–Ω —Ä–µ–∑—É–ª—å—Ç–∞—Ç —Ä–∞–±–æ—Ç—ã –º–µ—Ç–æ–¥–∞ getHtml().
document.getElementById("employeeClass").onclick = function employeeClass() { 
  const mainDiv = document.getElementById("content");
  const tableDiv = document.createElement("div");
  tableDiv.setAttribute("id", "content__tableDiv");
  tableDiv.className = "content__tableDiv";
  const tableTag = document.createElement("table");
  tableTag.setAttribute("id", "content__table");
  tableTag.className = "content__table";

  tableDiv.append(tableTag);
  mainDiv.append(tableDiv);

    class Employee {
      constructor(name, position, salary) {
        this.name = name;
        this.position = position;
        this.salary = salary;
      }
    }
    const arrEmployee = [
      new Employee("–§—Ä–æ–¥–æ –ë—ç–≥–≥–∏–Ω—Å", "–•–æ–±–±–∏—Ç", 6000),
      new Employee("–°—ç–º –ì—ç–º–¥–∂–∏", "–ü–æ–º–æ—â–Ω–∏–∫ —Ö–æ–±–±–∏—Ç–∞", 5000),
      new Employee("–ì—ç–Ω–¥–∞–ª—å—Ñ", "–í–æ–ª—à–µ–±–Ω–∏–∫", 5000),
      new Employee("–ì–∏–º–ª–∏", "–ì–Ω–æ–º", 4000),
      new Employee("–õ–µ–≥–æ–ª–∞—Å", "–≠–ª—å—Ñ", 4000),
      new Employee("–ê—Ä–∞–≥–æ—Ä–Ω", "–°—Ç—Ä–∞–Ω–Ω–∏–∫", 3000),
      new Employee("–°–∞—É—Ä–æ–Ω", "–¢—ë–º–Ω—ã–π –í–ª–∞—Å—Ç–µ–ª–∏–Ω", 6000),
      new Employee("–ì–æ–ª–ª—É–º ", "–ü—Ä–æ—Å—Ç–æ –ø—Ä–µ–ª–µ—Å—Ç—å", 4000),
    ];
    class EmpTable {
      constructor(arr) {
        this.arr = arr;
      }
      getHtml() {
        const table = document.getElementById("content__table");
        const array = this.arr;
        const head = document.createElement("tr");
        head.setAttribute("style", "font-size: 18px;");
        const th1 = document.createElement("th");
        th1.textContent = "–ò–º—è";
        const th2 = document.createElement("th");
        th2.textContent = "–î–æ–ª–∂–Ω–æ—Å—Ç—å";
        const th3 = document.createElement("th");
        th3.textContent = "–ó/–ü($)";
        head.append(th1);
        head.append(th2);
        head.append(th3);
        table.append(head);
        for (let i in array) {
          let tr = document.createElement("tr");
          table.append(tr);
          for (let j in array[i]) {
            let td = document.createElement("td");
            td.textContent = array[i][j];
            tr.append(td);
            td.setAttribute("style", "padding: 5px 10px; font-size: 18px;");
          }
        }
        table.setAttribute("border", "2");
        table.setAttribute("bordercolor", "orangered");
        table.setAttribute("width", "100%");
        table.setAttribute("style", "margin: auto; background-color: lightgrey; margin-bottom: 30px;");
      }
    }
    const tableObj = new EmpTable(arrEmployee);
    tableObj.getHtml();
    
};

};


